@use "uswds-core" as *;
@use "theme-variables" as *;

.digital-banner {
  @include u-bg("indigo-warm-80");

  a, a:hover, a:visited {
    @include u-text("white");
  }

  &__explore-link {
    display: flex;
    flex-direction: row;
    align-items: center;
  }

  &__explore-arrow {
    padding-left: .5rem;
  }
}
